:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.infl-suff-footnote-link {
  position: relative !important;
}
.alpheios-inflections__footnote-popup {
  display: grid;
  grid-template-columns: 20px 1fr;
  grid-row-gap: 2px !important;
  background: #FFF !important;
  color: #333333 !important;
  position: absolute !important;
  padding: 30px 15px 15px !important;
  left: 0 !important;
  bottom: 20px !important;
  z-index: 10 !important;
  min-width: 200px !important;
  border: 1px solid #4E6476 !important;
  cursor: move !important;
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -khtml-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}
.alpheios-inflections__footnote-popup.hidden {
  display: none;
}
.alpheios-inflections__footnote-popup-title {
  font-weight: 700 !important;
  position: absolute !important;
  text-transform: uppercase !important;
  left: 15px !important;
  top: 7px !important;
}
.alpheios-inflections__footnote-popup-close-btn {
  position: absolute !important;
  right: 5px !important;
  top: 5px !important;
  display: block;
  width: 20px !important;
  height: 20px !important;
  margin: 0 !important;
  cursor: pointer !important;
  fill: #4E6476 !important;
  stroke: #4E6476 !important;
}
.alpheios-inflections__footnote-popup-close-btn:hover,
.alpheios-inflections__footnote-popup-close-btn:active {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}