:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-table-sf__title {
  margin-bottom: 5px !important;
  padding-left: 30px !important;
}
.alpheios-clickable {
  cursor: pointer !important;
}
.alpheios-inflections__not-impl-msg {
  padding: 20px !important;
  font-size: 0.875rem !important;
}
.alpheios-inflections__table-ctrl-cont {
  display: flex;
  width: 100% !important;
  margin-bottom: 0.2rem !important;
  justify-content: flex-end !important;
}
.alpheios-inflections__table-ctrl-cell {
  flex-grow: 1 !important;
}
div.alpheios-inflections__table-ctrl-cont div.alpheios-inflections__table-ctrl-cell {
  font-size: 0.875rem !important;
}
.alpheios-inflections__table-ctrl-btn-cont {
  margin-left: 0.2rem !important;
}
.auk .alpheios-button-small.alpheios-inflections__control-btn {
  line-height: 1.6 !important;
  white-space: nowrap !important;
}
.auk .alpheios-button-small.alpheios-inflections__control-btn--right {
  margin-left: 0.5rem !important;
}
.infl-prdgm-tbl {
  display: table;
  border-top: 1px solid gray !important;
  border-left: 1px solid gray !important;
  margin-bottom: 30px !important;
}
.infl-prdgm-tbl__row {
  display: table-row;
}
.infl-prdgm-tbl__cell {
  display: table-cell;
  padding: 2px 5px !important;
  border-right: 1px solid gray !important;
  border-bottom: 1px solid gray !important;
  min-width: 20px !important;
}
.infl-prdgm-tbl-cell--label {
  font-weight: 700 !important;
}
.infl-table {
  display: grid;
  border-left: 1px solid #111 !important;
  border-bottom: 1px solid #111 !important;
  margin-bottom: 1rem !important;
}
.infl-table--wide {
  /* Data flow order: number- case - declension - gender - type*/
  grid-auto-flow: row !important;
  grid-template-columns: repeat(21, 1fr);
  /* Default value, will be redefined in JS if necessary */
}
.infl-table--narrow {
  /* Data flow order: declension - number- case - gender - type*/
  grid-auto-flow: row !important;
  grid-template-columns: repeat(6, 1fr);
  /* Default value, will be redefined in JS if necessary */
}
.infl-table.hidden {
  display: none;
}
.infl-table-narrow-views-cont {
  display: flex;
  flex-wrap: wrap !important;
}
.infl-cell {
  font-size: calc(var(--alpheios-base-font-size) * 0.75) !important;
  padding: 0 2px 0 5px !important;
  border-right: 1px solid #111 !important;
  border-top: 1px solid #111 !important;
  position: relative !important;
}
.infl-cell.hidden {
  display: none;
}
.infl-cell--hdr {
  font-weight: 700 !important;
  text-align: center !important;
}
.infl-cell--hdr .infl-cell__conj-stem {
  text-transform: none !important;
}
.infl-cell--fw {
  grid-column: 1/-1 !important;
  font-style: italic !important;
  text-transform: capitalize !important;
}
.infl-cell.infl-cell--sep {
  height: 50px !important;
}
.infl-cell--sp0 {
  display: none;
}
.infl-cell--sp1 {
  grid-column-end: span 1 !important;
}
.infl-cell--sp2 {
  grid-column-end: span 2 !important;
}
.infl-cell--sp3 {
  grid-column-end: span 3 !important;
}
.infl-cell--sp4 {
  grid-column-end: span 4 !important;
}
.infl-cell--sp5 {
  grid-column-end: span 5 !important;
}
.infl-cell--sp6 {
  grid-column-end: span 6 !important;
}
.infl-cell--sp7 {
  grid-column-end: span 7 !important;
}
.infl-cell--sp8 {
  grid-column-end: span 8 !important;
}
.infl-cell--sp9 {
  grid-column-end: span 9 !important;
}
.infl-cell--sp10 {
  grid-column-end: span 10 !important;
}
.infl-cell--sp11 {
  grid-column-end: span 11 !important;
}
.infl-cell--sp12 {
  grid-column-end: span 12 !important;
}
.infl-cell--sp13 {
  grid-column-end: span 13 !important;
}
.infl-cell--sp14 {
  grid-column-end: span 14 !important;
}
.infl-cell--sp15 {
  grid-column-end: span 15 !important;
}
.infl-cell--sp16 {
  grid-column-end: span 16 !important;
}
.infl-cell--sp17 {
  grid-column-end: span 17 !important;
}
.infl-cell--sp18 {
  grid-column-end: span 18 !important;
}
.infl-cell--sp19 {
  grid-column-end: span 19 !important;
}
.infl-cell--sp20 {
  grid-column-end: span 20 !important;
}
.infl-cell--sp21 {
  grid-column-end: span 21 !important;
}
.infl-cell--sp22 {
  grid-column-end: span 22 !important;
}
.infl-cell--sp23 {
  grid-column-end: span 23 !important;
}
.infl-cell--sp24 {
  grid-column-end: span 24 !important;
}
.infl-cell--hl {
  background: lightgray !important;
}
.infl-cell--morph-match,
.infl-table .infl-cell.infl-cell--morph-match {
  border: 3px solid #bce6f0 !important;
}
.infl-cell__conj-stem {
  text-transform: none !important;
}
.infl-suff {
  line-height: 1.25 !important;
  cursor: pointer !important;
}
.row-title-cell {
  text-transform: capitalize !important;
}
.infl-suff.infl-suff--suffix-match {
  background-color: #bce6f0 !important;
}
.infl-suff--full-match {
  background-color: lightgray !important;
}
.infl-suff.infl-suff--suffix-match.infl-suff--full-match {
  background-color: #F6D918 !important;
  font-weight: 700 !important;
}