:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-panel {
  z-index: 2000 !important;
  position: fixed !important;
  background: #FFF !important;
  resize: both !important;
  opacity: 0.95 !important;
  direction: ltr !important;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 60px auto 60px !important;
  grid-template-areas: "header" "content" "content" !important;
}
.alpheios-panel[data-notification-visible=true] {
  grid-template-areas: "header" "content" "notifications" !important;
}
.alpheios-panel.alpheios-panel-left {
  left: 0 !important;
  border-right: 1px solid #D1D1D0 !important;
}
.alpheios-panel.alpheios-panel-right {
  right: 0 !important;
  border-left: 1px solid #D1D1D0 !important;
  grid-template-columns: auto;
  grid-template-areas: "header" "content" "content" !important;
}
.alpheios-panel.alpheios-panel-right[data-notification-visible=true] {
  grid-template-areas: "header" "content" "notifications" !important;
}
.alpheios-panel__header {
  position: relative !important;
  display: flex;
  flex-wrap: nowrap !important;
  box-sizing: border-box !important;
  grid-area: header !important;
  flex-direction: row !important;
  justify-content: space-between !important;
  border-bottom: 1px solid #D1D1D0 !important;
}
.alpheios-panel-left .alpheios-panel__header {
  direction: ltr !important;
}
.alpheios-panel-right .alpheios-panel__header {
  direction: rtl !important;
}
.alpheios-panel__header-logo {
  flex-grow: 0 !important;
  justify-content: flex-start !important;
}
.alpheios-panel__header-selection {
  font-size: 16px !important;
  font-weight: 700 !important;
  color: #4E6476 !important;
}
.alpheios-panel__header-word {
  font-size: 14px !important;
  position: relative !important;
  top: -1px !important;
}
.auk .alpheios-panel__header-logo-img {
  width: auto !important;
  height: 30px !important;
  padding-top: 5px !important;
}
.alpheios-panel__header-action-btn,
.alpheios-panel__header-action-btn.active:hover,
.alpheios-panel__header-action-btn.active:focus {
  display: block;
  width: 20px !important;
  height: 20px !important;
  text-align: center !important;
  cursor: pointer !important;
  fill: #D1D1D0 !important;
  stroke: #D1D1D0 !important;
  margin: 10px 9px !important;
}
.alpheios-panel__header-action-btn svg,
.alpheios-panel__header-action-btn.active:hover svg,
.alpheios-panel__header-action-btn.active:focus svg {
  width: 20px !important;
  height: 20px !important;
  display: inline-block;
  vertical-align: top !important;
}
.alpheios-panel__header-action-btn.alpheios_close {
  stroke-width: 2.5 !important;
}
.alpheios-panel__header-action-btn.alpheios_left,
.alpheios-panel__header-action-btn.alpheios_right {
  stroke-width: 2 !important;
}
.alpheios-panel__header-action-btn:hover,
.alpheios-panel__header-action-btn:focus,
.alpheios-panel__header-action-btn.active {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}
.alpheios-panel__content {
  overflow: visible !important;
  grid-area: content !important;
  direction: ltr !important;
  box-sizing: border-box !important;
  display: flex;
  flex-flow: wrap !important;
  position: relative !important;
}
.alpheios-lookup__panel {
  display: block;
  border-bottom: 1px solid !important;
  margin-bottom: 20px !important;
}
.alpheios-panel__notifications {
  display: none;
  position: relative !important;
  padding: 10px 20px !important;
  background: #BCE5F0 !important;
  grid-area: notifications !important;
  overflow: hidden !important;
}
.alpheios-panel__notifications-close-btn {
  position: absolute !important;
  right: 5px !important;
  top: 5px !important;
  display: block;
  width: 20px !important;
  height: 20px !important;
  margin: 0 !important;
  cursor: pointer !important;
  fill: #D1D1D0 !important;
  stroke: #D1D1D0 !important;
}
.alpheios-panel__notifications-close-btn:hover,
.alpheios-panel__notifications-close-btn:focus {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}
.alpheios-panel__notifications--lang-switcher {
  font-size: 12px !important;
  float: right !important;
  margin: -20px 10px 0 0 !important;
  display: inline-block;
}
.alpheios-panel__notifications--lang-switcher .alpheios-select {
  width: 120px !important;
  height: 25px !important;
}
.alpheios-panel__notifications--important {
  background: #73CDDE !important;
}
[data-notification-visible=true] .alpheios-panel__notifications {
  display: block;
}
.alpheios-panel__tab-panel {
  display: flex;
  flex-direction: column !important;
  padding: 20px !important;
  width: 100% !important;
}
.alpheios-panel__tab-panel--fw {
  width: 100% !important;
}
.alpheios-panel__tab-panel--no-padding {
  padding: 0 !important;
}
.alpheios-panel__content_no_top_padding {
  padding-top: 0 !important;
}
.alpheios-panel__message {
  margin-bottom: 0.5rem !important;
}
.alpheios-panel__options-item {
  margin-bottom: 0.5rem !important;
  max-width: 300px !important;
}
.alpheios-panel__contentitem {
  margin-bottom: 1em !important;
}
.alpheios-panel__header-btn-group--center {
  direction: ltr !important;
  display: flex;
  flex-wrap: nowrap !important;
  box-sizing: border-box !important;
}
.alpheios-panel__header-btn-group--end {
  display: flex;
  flex-wrap: nowrap !important;
  justify-content: flex-end !important;
  box-sizing: border-box !important;
}
.alpheios-panel__tab__options {
  width: 100% !important;
  max-width: 600px !important;
}
.alpheios-panel__options-item {
  max-width: none !important;
  width: 100% !important;
}
.alpheios-panel__options-item .alpheios-select:not([multiple]):not([size]) {
  max-width: 235px !important;
  display: inline-block;
  vertical-align: top !important;
}
.alpheios-panel__options-item .alpheios-setting__label {
  width: 100px !important;
  display: inline-block;
}
.alpheios-panel__options-item select {
  display: inline-block;
}
.alpheios-panel__tab__inflections {
  width: 100% !important;
}
.alpheios-panel__tab__inflectionsbrowser {
  width: 100% !important;
}
.alpheios-panel__menu-btn {
  width: 40px !important;
  height: 40px !important;
  margin: 10px 10px 10px 30px !important;
  cursor: pointer !important;
}
.alpheios-panel__menu-icon {
  width: 40px !important;
  height: 40px !important;
  fill: #D1D1D0 !important;
}
.alpheios-panel__menu-icon:hover,
.alpheios-panel__menu-icon:focus {
  fill: #5BC8DC !important;
}
.alpheios-panel--compact {
  height: 50vh !important;
  width: 100% !important;
  left: 0 !important;
  bottom: 0 !important;
  border-top: 1px solid #D1D1D0 !important;
}
.alpheios-panel--compact .alpheios-panel__content {
  overflow: auto !important;
}
.alpheios-panel--compact.auk .alpheios-panel__header-action-btn {
  width: 40px !important;
  height: 40px !important;
}
.alpheios-panel--compact.auk .alpheios-panel__header-action-btn {
  width: 40px !important;
  height: 40px !important;
}
.alpheios-panel--compact.auk .alpheios-panel__header-action-btn svg {
  width: 40px !important;
  height: 40px !important;
}