:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-panel__tab-panel.alpheios-panel__tab__inflections {
  padding: 0 0 20px !important;
}
.alpheios-inflections__placeholder {
  padding: 0 20px !important;
  margin-bottom: 1rem !important;
}
.alpheios-inflections__content {
  padding: 0 20px !important;
  border-bottom: 1px solid #d3d3d3 !important;
}
h3.alpheios-inflections__title {
  line-height: 1 !important;
  margin: 0 0 0.6rem 0 !important;
  font-weight: 700 !important;
  text-align: center !important;
}
h4.alpheios-inflections__additional_title {
  line-height: 1.6 !important;
  font-weight: bold !important;
  text-align: left !important;
  margin: 0 0 0.6rem 0 !important;
}
.auk .alpheios-select.alpheios-inflections__view-selector {
  height: auto !important;
  max-width: 220px !important;
  line-height: 1.6 !important;
}
.alpheios-inflections__actions {
  display: flex;
  flex-direction: row !important;
  align-items: flex-end !important;
  justify-content: space-between !important;
  margin-bottom: 0.6rem !important;
  margin-top: 10px !important;
}
.alpheios-inflections__form {
  font-weight: bold !important;
  line-height: 1.2 !important;
  justify-content: flex-start !important;
}
.alpheios-inflections__placeholder {
  text-align: center !important;
}
.alpheios-inflections__footnotes {
  display: none;
  grid-template-columns: 1.6rem 1fr;
  font-size: 0.875rem !important;
  line-height: 1.2 !important;
  margin-bottom: 2rem !important;
}
.alpheios-inflections__footnotes dt {
  font-weight: 700 !important;
}
[data-footnote] {
  position: relative !important;
  padding-left: 2px !important;
  vertical-align: super !important;
}
.alpheios-inflections__credits-cont {
  margin-bottom: 10px !important;
}
h3.alpheios-inflections__credits-title {
  font-size: 16px !important;
  font-weight: 700 !important;
  color: #4E6476 !important;
  margin-bottom: 0 !important;
}
.alpheios-inflections__credits-text {
  font-size: 12px !important;
  font-weight: normal !important;
  color: #7E8897 !important;
  font-style: italic !important;
  padding: 5px !important;
}
.alpheios-inflections__paradigms-expl {
  font-size: 12px !important;
  font-weight: normal !important;
  color: #7E8897 !important;
  font-style: italic !important;
  margin: 20px 0 10px !important;
}
.alpheios-inflections__paradigms-expl span {
  color: #4E6476 !important;
  font-weight: 700 !important;
}
.alpheios-inflections__supp-tables {
  margin-top: 4rem !important;
}
.alpheios-inflections__progress-wrapper {
  height: 1.2rem !important;
  margin: 0 1rem 2rem !important;
  font-size: 0.875rem !important;
}
.alpheios-inflections__progress-border {
  border: 2px solid #73CDDE !important;
  min-width: 300px !important;
  height: 100% !important;
  padding: 2px !important;
}
.alpheios-inflections__progress-whitespace {
  overflow: hidden !important;
  height: 100% !important;
  margin: 0 auto !important;
  position: relative !important;
}
.alpheios-inflections__progress-line {
  position: absolute !important;
  height: 100% !important;
  width: 100% !important;
  background-color: #73CDDE !important;
  animation: cssload-slide 5.75s steps(40) infinite;
}
.alpheios-inflections__progress-whitespace div.alpheios-inflections__progress-text,
.alpheios-inflections__progress-text {
  text-transform: uppercase !important;
  color: #666666 !important;
  position: absolute !important;
  width: 100% !important;
  text-align: center !important;
  white-space: nowrap !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  min-width: 300px !important;
  font-size: 13px !important;
  top: 3px !important;
}
@keyframes cssload-slide {
0% {
    left: -100%;
}
100% {
    left: 100%;
}
}
@-o-keyframes cssload-slide {
0% {
    left: -100%;
}
100% {
    left: 100%;
}
}
@-ms-keyframes cssload-slide {
0% {
    left: -100%;
}
100% {
    left: 100%;
}
}
@-webkit-keyframes cssload-slide {
0% {
    left: -100%;
}
100% {
    left: 100%;
}
}
@-moz-keyframes cssload-slide {
0% {
    left: -100%;
}
100% {
    left: 100%;
}
}