:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-overlay-nav {
  background: transparent !important;
  position: fixed !important;
  top: 10px !important;
  right: 15px !important;
  z-index: 10000 !important;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
}
.alpheios-overlay-nav .alpheios-navbuttons__btn {
  background: #FFF !important;
  border: 1px solid #B8B7B5 !important;
  padding: 5px !important;
}
.alpheios-overlay-nav .alpheios-navbuttons__btn {
  width: 40px !important;
  height: 40px !important;
  margin: 10px 0 !important;
  box-sizing: border-box !important;
  position: relative !important;
  padding: 0 !important;
}
.alpheios-overlay-nav .alpheios-navbuttons__btn svg {
  width: 20px !important;
  height: 20px !important;
  top: 9px !important;
  left: 0 !important;
  position: relative !important;
  box-sizing: border-box !important;
}
.alpheios-overlay-nav .alph_tooltip {
  position: relative !important;
}
.alpheios-overlay-nav .alph_tooltip .tooltiptext {
  visibility: hidden !important;
  position: absolute !important;
  width: 120px !important;
  text-align: center !important;
  z-index: 1 !important;
  opacity: 0 !important;
  font-size: 12px !important;
  display: none;
  padding: 5px 0 !important;
  border-radius: 6px !important;
  transition: opacity 0.6s ease 0s !important;
  background-color: #fcfcfa !important;
  color: #4e6476 !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: #4e6476 !important;
  border-image: initial !important;
}
.alpheios-overlay-nav .alph_tooltip .alph_tooltip-left {
  top: 0 !important;
  bottom: auto !important;
  right: 128% !important;
}
.alpheios-overlay-nav .alph_tooltip .alph_tooltip-left::after {
  content: "" !important;
  position: absolute !important;
  top: 50% !important;
  left: 100% !important;
  margin-top: -5px !important;
  border-width: 5px !important;
  border-style: solid !important;
  border-color: transparent transparent transparent #555 !important;
}
.alpheios-overlay-nav .lph_tooltip-left {
  top: 5px !important;
  bottom: auto !important;
  right: 128% !important;
}
.alpheios-overlay-nav .lph_tooltip-left::after {
  content: "" !important;
  position: absolute !important;
  top: 50% !important;
  left: 100% !important;
  margin-top: -5px !important;
  border-width: 5px !important;
  border-style: solid !important;
  border-color: transparent transparent transparent #555 !important;
}
.alpheios-overlay-nav .alph_tooltip:hover .tooltiptext {
  visibility: visible !important;
  opacity: 1 !important;
  display: inline;
}
.alpheios-overlay-nav .alpheios-overlay-nav__header {
  height: 20px !important;
  width: 40px !important;
  box-sizing: border-box !important;
  border: 1px solid #B8B7B5 !important;
  cursor: pointer !important;
  background: #FFF !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__header svg {
  width: 20px !important;
  height: auto !important;
  position: relative !important;
  left: 9px !important;
  top: -5px !important;
  fill: #B8B7B5 !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__header.expanded svg {
  top: 5px !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__header:hover svg {
  fill: #5BC8DC !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__drag-handle {
  width: 40px !important;
  height: 30px !important;
  border: 1px solid #B8B7B5 !important;
  border-bottom: none !important;
  background: #FFF !important;
  box-sizing: border-box !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__logo-icon {
  width: 30px !important;
  height: auto !important;
  position: relative !important;
  top: 3px !important;
  left: 4px !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup-control {
  cursor: pointer !important;
  background: #FFF !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup-control .alpheios-navbuttons__btn {
  height: 30px !important;
  margin: 0 !important;
  border-bottom: none !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup-control .alpheios-navbuttons__btn svg {
  top: 4px !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup {
  display: flex;
  position: absolute !important;
  width: 400px !important;
  height: 80px !important;
  background: #FFF !important;
  left: -400px !important;
  top: 0 !important;
  border: 1px solid #B8B7B5 !important;
  border-right: none !important;
  box-sizing: border-box !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup .alpheios-lookup__form {
  margin-top: 30px !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup input {
  width: 300px !important;
  box-sizing: border-box !important;
  line-height: 28px !important;
  box-shadow: none !important;
  padding: 0 10px !important;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
  font-size: 14px !important;
  height: 30px !important;
  border: 1px solid #666666 !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup input:focus {
  border-color: #5BC8DC !important;
  outline: transparent !important;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup .alph_tooltip {
  display: inline-block;
}
.alpheios-overlay-nav .alpheios-overlay-nav__lookup button {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
  font-size: 12px !important;
  line-height: 30px !important;
  background-color: #4E6476 !important;
  color: #FFF !important;
  text-transform: uppercase !important;
  border-radius: 0 !important;
  border: none !important;
  height: 30px !important;
  cursor: pointer !important;
}