:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.infl-prdgm-tbl {
  display: table;
  border-top: 1px solid gray !important;
  border-left: 1px solid gray !important;
  margin-bottom: 30px !important;
}
.infl-prdgm-tbl__row {
  display: table-row;
}
.infl-prdgm-tbl__cell {
  display: table-cell;
  padding: 2px 5px !important;
  border-right: 1px solid gray !important;
  border-bottom: 1px solid gray !important;
}
.infl-prdgm-tbl__cell--label {
  font-weight: 700 !important;
}