:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
#alpheios-reskin-panel.alpheios-skin_panel {
  display: inline-block;
}
ul.alpheios-skin_properties {
  margin: 0 !important;
  padding: 0 30px 10px 10px !important;
  list-style: none !important;
  max-width: 250px !important;
  display: inline-block;
}
#alpheios-reskin-panel .alpheios-skin_properties_item {
  display: inline-block;
  vertical-align: middle !important;
  text-align: center !important;
  color: #fff !important;
  background: #4E6476 !important;
  cursor: pointer !important;
  padding: 0 !important;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
  text-indent: 0 !important;
  list-style-type: none !important;
}
#alpheios-reskin-panel .alpheios-skin_properties_item__light_bg,
#alpheios-reskin-panel .alpheios-skin_properties_item__dark_bg {
  border-radius: 0 !important;
  background: transparent !important;
}
#alpheios-reskin-panel .alpheios-skin_properties_item__light_bg svg,
#alpheios-reskin-panel .alpheios-skin_properties_item__dark_bg svg {
  width: 90% !important;
  height: 90% !important;
}
.alpheios-font_small_class #alpheios-reskin-panel .alpheios-skin_properties_item__small_font {
  background: #7E8897 !important;
}
.alpheios-font_medium_class #alpheios-reskin-panel .alpheios-skin_properties_item__medium_font {
  background: #7E8897 !important;
}
.alpheios-font_large_class #alpheios-reskin-panel .alpheios-skin_properties_item__large_font {
  background: #7E8897 !important;
}