:root {
  --alpheios-base-font-size: 16px !important;
  --alpheios-color-neutral-dark: #757575 !important;
  --alpheios-color-neutral-light: #E6E6E6 !important;
  --alpheios-color-neutral-lightest: #FFF !important;
  --alpheios-color-dark: #0E2233 !important;
  --alpheios-color-dark-hover: #1B374F !important;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark) !important;
  --alpheios-color-muted: #185F6D !important;
  --alpheios-color-muted-hover: #124D59 !important;
  --alpheios-color-muted-pressed: #093943 !important;
  --alpheios-color-vivid: #C64906 !important;
  --alpheios-color-vivid-hover: #F27431 !important;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant) !important;
  --alpheios-color-bright: #FFC24F !important;
  --alpheios-color-bright-hover: #FFD27D !important;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright) !important;
  --alpheios-color-light: #BCE5F0 !important;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest) !important;
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid) !important;
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid) !important;
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-popup {
  display: flex;
  flex-direction: column !important;
  background: #FFF !important;
  border: 1px solid lightgray !important;
  min-width: 210px !important;
  min-height: 150px !important;
  z-index: 1000 !important;
  position: fixed !important;
  left: 200px !important;
  top: 100px !important;
  box-sizing: border-box !important;
  /* Required for Interact.js to take element size with paddings and work correctly */
  overflow: auto !important;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
  font-size: 16px !important;
  color: #666666 !important;
}
.alpheios-popup__header {
  position: relative !important;
  box-sizing: border-box !important;
  width: 100% !important;
  flex: 0 0 45px !important;
  padding: 10px 10px 0 !important;
  margin-top: 20px !important;
  display: flex;
  justify-content: space-between !important;
  align-items: flex-start !important;
}
.alpheios-popup__header-text {
  line-height: 1 !important;
  align-items: flex-start !important;
  padding: 7px 20px 0 0 !important;
}
.alpheios-popup__header-text[lang=ara] {
  padding: 0px 20px 0px 20px !important;
  /* the arabic amiri font does not like the top padding */
}
.alpheios-popup__header-selection {
  font-size: 16px !important;
  font-weight: 700 !important;
  color: #4E6476 !important;
}
.alpheios-popup__header-word {
  font-size: 12px !important;
  position: relative !important;
  top: -1px !important;
}
.alpheios-popup__close-btn {
  display: block;
  position: absolute !important;
  width: 20px !important;
  right: 5px !important;
  top: 2px !important;
  cursor: pointer !important;
  fill: #D1D1D0 !important;
  stroke: #D1D1D0 !important;
  background: inherit !important;
  stroke-width: 2.5 !important;
}
.alpheios-popup__close-btn:hover,
.alpheios-popup__close-btn:focus {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}
.alpheios-popup__notifications {
  display: none;
  position: relative !important;
  padding: 10px 20px !important;
  background: #BCE5F0 !important;
  flex: 0 0 60px !important;
  box-sizing: border-box !important;
  overflow: hidden !important;
}
.alpheios-popup__notifications-close-btn {
  position: absolute !important;
  right: 5px !important;
  top: 5px !important;
  display: block;
  width: 20px !important;
  height: 20px !important;
  margin: 0 !important;
  cursor: pointer !important;
  fill: #D1D1D0 !important;
  stroke: #D1D1D0 !important;
}
.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}
[data-notification-visible=true] .alpheios-popup__notifications {
  display: block;
}
.alpheios-popup__notifications--lang-switcher {
  font-size: 12px !important;
  float: right !important;
  margin: -20px 10px 0 0 !important;
  display: inline-block;
}
.alpheios-popup__notifications--lang-switcher .alpheios-select {
  width: 120px !important;
  height: 25px !important;
}
.alpheios-popup__notifications--important {
  background: #73CDDE !important;
}
.alpheios-popup__morph-cont {
  flex: 1 1 !important;
  box-sizing: border-box !important;
  margin: 5px 10px 0 !important;
  overflow: auto !important;
  padding: 10px !important;
  border: 1px solid #B8B7B5 !important;
}
.alpheios-popup__morph-cont-providers-header {
  display: inline-block;
  /* color: $alpheios-link-color;
  font-size: 0.75*$alpheios-base-font-size; */
  font-weight: 700 !important;
  margin-top: 2px !important;
}
.alpheios-popup__definitions--placeholder {
  border: 0 none !important;
  padding: 10px 0 !important;
}
img.alpheios-popup__logo {
  height: 16px !important;
  width: auto !important;
  vertical-align: middle !important;
}
.alpheios-popup__more-btn {
  float: right !important;
  margin-bottom: 10px !important;
  /* font-size: 0.675 * $alpheios-base-font-size; */
}
.alpheios-popup__morph-cont-providers-source {
  font-size: smaller !important;
  font-weight: normal !important;
  /* color: $alpheios-toolbar-color; */
  font-style: italic !important;
  margin-left: 0.5em !important;
  margin-top: 0.5em !important;
  max-width: 700px !important;
}
.alpheios-popup__providers {
  margin: 0 0 5px 10px !important;
}
.alpheios-popup__providers-link {
  display: inline-block;
  vertical-align: middle !important;
  padding: 5px 0 0 !important;
}
.alpheios-popup__providers-link:hover {
  text-decoration: underline !important;
}